                      class="col-6 tk-px-padding-px10 tk-position-relative"
                      style="height: 240px"
                    >
                      <line-chart
                        :height="200"
                        :chart-data="reOpensData"
                        :extra-options="chartConfigs.blueChartOptions"
                        v-if="reOpensData.datasets.length > 0"
                      >
                      </line-chart>
                      <loading v-else></loading>
                    </div>
                    <div
                      class="col-6 tk-px-padding-px10 tk-position-relative"
                      style="height: 240px"
                    >
                      <bar-chart
                        :height="200"
                        :chart-data="reOpensPlatform"
                        v-if="reOpensPlatform.datasets.length > 0"
                      >
                      </bar-chart>
                      <loading v-else></loading>
                    </div>
                  </div>
                </div>
              </div>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
      </el-table-column>
    </el-table>

    <modal :show.sync="showArchive">
      <template slot="header">
        <h5 class="modal-title">Archive Coupon</h5>
      </template>
      <div class="tk-popup-content">
        <span>Would you like to achive this link?</span>
      </div>
      <template slot="footer">
        <base-button
          type="secondary"
          @click.prevent="showArchive = false"
          class="tk-font-color-lightGray tk-border-none"
          >Go back</base-button
        >
        <button-with-loading
          style="margin: 0 0 0 1rem"
          text="Archive now"
          @start="archiveCoupon"
          :type="'main'"
          :complete="isButtonLoadingComplete"
        ></button-with-loading>
      </template>
    </modal>

    <modal :show.sync="showEnable">
      <template slot="header">
        <h5 class="modal-title">Enable Coupon</h5>
      </template>
      <div class="tk-popup-content">
        <span>
          <div class="form-group card-wrapper tk-px-margin-bottom-px0">
            <div class="card tk-px-margin-bottom-px16">
              <div class="form-control" style="height: auto">
                <span
                  class="tk-layout-inline-block tk-font-color-black1"
                  style="width: calc(100% - 56px)"
                >
                  <span v-if="!expirationOpen">Expiration</span>
                  <template v-else>
                    <span>Expiration date</span>
                    <flat-picker
                      :config="{ allowInput: true }"
                      class="form-control form-control-muted tk-bg-color-gray-100 datepicker tk-layout-inline-block tk-px-width-px100 tk-px-margin-left-px10 tk-px-height-px36"
                      v-model="expiration"
                    >
                    </flat-picker>
                  </template>
                </span>
                <base-switch
                  class="mr-1 tk-layout-inline-block"
                  v-model="expirationOpen"
                  on-text="On"
                  off-text="Off"
                  type="main"
                ></base-switch>
              </div>
            </div>
          </div>
        </span>
      </div>
      <template slot="footer">
        <base-button
          type="secondary"
          @click.prevent="showEnable = false"
          class="tk-font-color-lightGray tk-border-none"
          >Close</base-button
        >
        <button-with-loading
          style="margin: 0 0 0 1rem"
          text="Enable"
          @start="enableCoupon"
          :type="'main'"
          :complete="isButtonLoadingComplete"
        ></button-with-loading>
      </template>
    </modal>
  </div>
</template>

<script>
// //  echarts  echarts 
// import * as echarts from "echarts/core";
// //  Chart
// import { BarChart, LineChart } from "echarts/charts";
// //  Component
// import {
//   TitleComponent,
//   TooltipComponent,
//   GridComponent,
// } from "echarts/components";
// //  Canvas 
// import { CanvasRenderer } from "echarts/renderers";
// // 
// echarts.use([
//   TitleComponent,
//   TooltipComponent,
//   GridComponent,
//   BarChart,
//   LineChart,
//   CanvasRenderer,
// ]);

import bus from "@/bus.js";
import htmlToPdf from "@/util/htmlToPDF";
import collections from "@/services/collections";
import flatPicker from "vue-flatpickr-component";
import buttonWithLoading from "@/tkViews/components/layout/ButtonWithLoading";
import * as chartConfigs from "@/components/Charts/config";
import LineChart from "@/components/Charts/LineChart";
import BarChart from "@/components/Charts/BarChart";
import {
  Table,
  TableColumn,
  DropdownMenu,
  DropdownItem,
  Dropdown,
  Tooltip,
} from "element-ui";
import Loading from "../../components/layout/Loading.vue";
export default {
  props: {
    salesManParams: {
      type: Array,
      default() {
        return [];
      },
    },
    accessToken: {
      type: String,
      default: "",
    },
    from: {
      type: String,
      default: "",
    },
    to: {
      type: String,
      default: "",
    },
  },
  components: {
    [Tooltip.name]: Tooltip,
    [Table.name]: Table,
    [TableColumn.name]: TableColumn,
    [Dropdown.name]: Dropdown,
    [DropdownItem.name]: DropdownItem,
    [DropdownMenu.name]: DropdownMenu,
    flatPicker,
    buttonWithLoading,
    Loading,
    LineChart,
    BarChart,
  },
  data() {
    return {
      data: [],
      sales: [],
      name: "",
      email: "",

      showArchive: false,
      showEnable: false,
      row: null,
      expiration: "",
      expirationOpen: false,
      isButtonLoadingComplete: false,

      stats: null,
      statsLink: "",
      signups: "",

      chartConfigs,
      clicksData: {
        datasets: [],
        labels: [],
        option: {},
      },
      clicksPlatform: {
        datasets: [],
        labels: [],
        option: {},
      },
      redirectsData: {
        datasets: [],
        labels: [],
        option: {},
      },
      redirectsPlatform: {
        datasets: [],
        labels: [],
        option: {},
      },
      installsData: {
        datasets: [],
        labels: [],
        option: {},
      },
      installsPlatform: {
        datasets: [],
        labels: [],
        option: {},
      },
      firstOpensData: {
        datasets: [],
        labels: [],
        option: {},
      },
      firstOpensPlatform: {
        datasets: [],
        labels: [],
        option: {},
      },
      reOpensData: {
        datasets: [],
        labels: [],
        option: {},
      },
      reOpensPlatform: {
        datasets: [],
        labels: [],
        option: {},
      },
    };
  },
  async created() {
    let self = this;
    self.initAnalytics();
  },
  mounted() {
    bus.$on("analytic", () => {
      this.initAnalytics();
    });
  },
  watch: {
    salesManParams: {
      handler(newVal, oldval) {
        console.log("");
        this.initAnalytics();
      },
      deep: true,
    },
    expirationOpen(newVal, oldVal) {
      if (newVal) {
        this.expiration = this.$tools.formatYYMMDD(new Date().getTime());
      } else {
        this.expiration = "";
      }
    },
  },
  methods: {
    clearChartData() {
      let self = this;
      self.clicksData = {
        datasets: [],
        labels: [],
        option: {},
      };
      self.redirectsData = {
        datasets: [],
        labels: [],
        option: {},
      };
      self.installsData = {
        datasets: [],
        labels: [],
        option: {},
      };
      self.firstOpensData = {
        datasets: [],
        labels: [],
        option: {},
      };
      self.reOpensData = {
        datasets: [],
        labels: [],
        option: {},
      };
      self.clicksPlatform = {
        datasets: [],
        labels: [],
        option: {},
      };
      self.redirectsPlatform = {
        datasets: [],
        labels: [],
        option: {},
      };
      self.installsPlatform = {
        datasets: [],
        labels: [],
        option: {},
      };
      self.firstOpensPlatform = {
        datasets: [],
        labels: [],
        option: {},
      };
      self.reOpensPlatform = {
        datasets: [],
        labels: [],
        option: {},
      };
    },
    async initEvent(row, type) {
      let self = this;

      self.clearChartData();

      let chartData = {
        datasets: [
          {
            label: await self.initChartEventName(type),
            data: [],
          },
        ],
        labels: [],
      };
      let days = await self.$tools.getDaysByDateString(self.from, self.to);
      let obj;

      if (days <= 30) {
        // chart by days
        obj = await self.initChartLabels(days, 0);
        chartData.labels = obj.label;
      } else if (days > 30 && days < 30 * 7) {
        // chart by wks
        obj = await self.initChartLabels(days, 1);
        chartData.labels = obj.label;
      } else if (days >= 30 * 7 && days < 30 * 30) {
        // chart by mons
        obj = await self.initChartLabels(days, 2);
        chartData.labels = obj.label;
      } else if (days >= 30 * 30) {
        // chart by yrs
        obj = await self.initChartLabels(days, 3);
        chartData.labels = obj.label;
      }

      chartData.datasets[0].data = await self.initChartEventData(
        row,
        obj.dates,
        type
      );

      console.log(chartData);

      let options = {
        tooltip: {
          trigger: "axis",
        },
        // toolbox: {
        //     show: true,
        //     feature: {
        //         dataZoom: {
        //             yAxisIndex: 'none'
        //         },
        //         dataView: {readOnly: false},
        //         magicType: {type: ['line', 'bar']},
        //         restore: {},
        //         saveAsImage: {}
        //     }
        // },
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: chartData.labels,
        },
        yAxis: {
          type: "value",
          // axisLabel: {
          //   formatter: "{value} C",
          // },
        },
        series: [
          {
            // name: '',
            type: "line",
            data: chartData.datasets[0].data,
            smooth: true,
            label: {
              show: true,
              position: "top",
            },
            symbolSize: 8,
            markLine: {
              data: [{ type: "average", name: "Average" }],
            },
          },
        ],
      };

      chartData.options = options;

      switch (type) {
        case "CLICK":
          self.clicksData = chartData;
          break;
        case "REDIRECT":
          self.redirectsData = chartData;
          break;
        case "APP_INSTALL":
          self.installsData = chartData;
          break;
        case "APP_FIRST_OPEN":
          self.firstOpensData = chartData;
          break;
        case "APP_RE_OPEN":
          self.reOpensData = chartData;
          break;
      }
    },
    async initPlatform(row, type) {
      let self = this;

      self.clearChartData();

      let chartData = {
        datasets: [
          {
            label: await self.initChartEventName(type),
            data: [],
          },
        ],
        labels: [],
      };

      let data = await self.initChartPlatformData(row, type);

      chartData.datasets[0].data = data.data;
      chartData.labels = data.labels;
      console.log(chartData);

      switch (type) {
        case "CLICK":
          self.clicksPlatform = chartData;
          break;
        case "REDIRECT":
          self.redirectsPlatform = chartData;
          break;
        case "APP_INSTALL":
          self.installsPlatform = chartData;
          break;
        case "APP_FIRST_OPEN":
          self.firstOpensPlatform = chartData;
          break;
        case "APP_RE_OPEN":
          self.reOpensPlatform = chartData;
          break;
      }
    },
    initChartEventName(type) {
      switch (type) {
        case "CLICK":
          return "Clicks";
        case "REDIRECT":
          return "Redirects";
        case "APP_INSTALL":
          return "Installs";
        case "APP_FIRST_OPEN	":
          return "First-opens";
        case "APP_RE_OPEN":
          return "Re-opens";
      }
    },
    async initChartEventData(row, dates, event) {
      let self = this;
      let data = [];
      let nowYMD = self.$tools.formatYYMMDD(new Date().getTime());
      for (let i = 0; i < dates.length; i++) {
        let url1 =
          "https://firebasedynamiclinks.googleapis.com/v1/https%3A%2F%2Ftunekey.app%2Flink%2F" +
          row.link.split("https://tunekey.app/link/")[1] +
          "/linkStats?durationDays=" +
          self.$tools.getDaysByDateString(dates[i], nowYMD);
        let url2 =
          "https://firebasedynamiclinks.googleapis.com/v1/https%3A%2F%2Ftunekey.app%2Flink%2F" +
          row.link.split("https://tunekey.app/link/")[1] +
          "/linkStats?durationDays=" +
          self.$tools.getDaysByDateString(
            i === dates.length - 1 ? self.to : dates[i + 1],
            nowYMD
          );
        let stats1 = await self.getStats(url1);
        let stats2 = await self.getStats(url2);
        // stats1 >= stats2
        if (!self.$tools.isNull(stats1)) {
          let count1 = 0;

          for (let j = 0; j < stats1.length; j++) {
            if (stats1[j].event === event) {
              count1 += parseInt(stats1[j].count);
            }
          }

          if (!self.$tools.isNull(stats2)) {
            let count2 = 0;

            for (let j = 0; j < stats2.length; j++) {
              if (stats2[j].event === event) {
                count2 += parseInt(stats2[j].count);
              }
            }

            count1 = count1 - count2;
          }

          data.push(count1);
        } else {
          data.push(0);
        }
      }

      return data;
    },
    async initChartPlatformData(row, event) {
      let self = this;
      let data = [];
      let labels = [];
      let days1 = self.$tools.getDaysByDateString(
        self.from,
        self.$tools.formatYYMMDD(new Date().getTime())
      );
      let days2 = self.$tools.getDaysByDateString(
        self.to,
        self.$tools.formatYYMMDD(new Date().getTime())
      );
      let url1 =
        "https://firebasedynamiclinks.googleapis.com/v1/https%3A%2F%2Ftunekey.app%2Flink%2F" +
        row.link.split("https://tunekey.app/link/")[1] +
        "/linkStats?durationDays=" +
        days1;
      let url2 =
        "https://firebasedynamiclinks.googleapis.com/v1/https%3A%2F%2Ftunekey.app%2Flink%2F" +
        row.link.split("https://tunekey.app/link/")[1] +
        "/linkStats?durationDays=" +
        days2;
      let stats1 = await self.getStats(url1);
      let stats2 = await self.getStats(url2);
      // stats1 >= stats2
      if (!self.$tools.isNull(stats1)) {
        for (let j = 0; j < stats1.length; j++) {
          if (stats1[j].event === event) {
            data.push(parseInt(stats1[j].count));
            labels.push(stats1[j].platform);
          }
        }

        if (!self.$tools.isNull(stats2)) {
          for (let j = 0; j < stats2.length; j++) {
            if (stats2[j].event === event) {
              labels.some((item, index) => {
                if (item === stats2[j].platform) {
                  data[index] -= parseInt(stats2[j].count);
                }
              });
            }
          }
        }
      } else {
        data.push(0);
        labels.push("none");
      }
      return {
        data,
        labels,
      };
    },
    initChartLabels(days, type) {
      let self = this;
      let label = [];
      let dates = [];
      switch (type) {
        case 0:
          // days
          for (let i = 0; i < days; i++) {
            let d = new Date(self.from);
            d.setDate(d.getDate() + i);
            let date = self.$tools.formatYYMMDD(d.getTime());
            dates.push(date);
            label.push(date.substring(5, date.length));
          }
          break;
        case 1:
          let yushu = days % 7;
          for (let i = 0; i < parseInt(days / 7) + (yushu === 0 ? 0 : 1); i++) {
            let d = new Date(self.from);
            d.setDate(d.getDate() + i * 7);
            let date = self.$tools.formatYYMMDD(d.getTime());
            let date2;
            if (d.getTime() >= new Date().getTime()) {
              date = date2 = self.$tools.formatYYMMDD(new Date().getTime());
            } else {
              d.setDate(d.getDate() + 7);
              date2 = self.$tools.formatYYMMDD(d.getTime());
            }

            dates.push(date);

            if (date2 === date) {
              label.push(date.substring(5, date.length));
            } else {
              label.push(
                date.substring(5, date.length) +
                  "~" +
                  date2.substring(5, date2.length)
              );
            }
          }
          // wks
          break;
        case 2:
          // mons
          let month = new Date(self.from).getMonth();
          for (let i = 0; i < days; i++) {
            let d = new Date(self.from);
            d.setDate(d.getDate() + i);
            if (d.getMonth() === month) {
              let date = self.$tools.formatYYMMDD(d.getTime());
              let daysInMonth = self.$tools.getDaysInMonth(date);
              let diff = daysInMonth - d.getDate();

              i += diff;

              dates.push(date);
              label.push(self.$tools.getMonthStr(month + 1));

              month++;
              if (month > 11) {
                month = 0;
              }
            }
          }
          break;
        case 3:
          // yrs
          let year = new Date(self.from).getFullYear();
          for (let i = 0; i < days; i++) {
            let d = new Date(self.from);
            d.setDate(d.getDate() + i);
            if (d.getFullYear() === year) {
              let date = self.$tools.formatYYMMDD(d.getTime());
              let daysInYear = self.$tools.getDaysInYear(date);
              let diff = daysInYear - self.$tools.getDaysthOfYear(date);

              i += diff;

              dates.push(date);
              label.push(date.split("/")[0]);

              year++;
            }
          }
          break;
      }
      return {
        label,
        dates,
      };
    },
    async initAnalytics() {
      let self = this;
      let days = self.$tools.getDaysByDateString(self.from, self.to);
      console.log("days:", days);
      self.data = [];
      setTimeout(() => {
        if (self.salesManParams.length > 0) {
          document.getElementsByClassName("el-table__empty-text")[0].innerText =
            "Loading...";
        } else {
          document.getElementsByClassName("el-table__empty-text")[0].innerText =
            "No data";
        }
      }, 100);
      console.log(self.from, self.to);
      for (let i = 0; i < self.salesManParams.length; i++) {
        let item = self.salesManParams[i];
        item.clicks = 0;
        item.redirects = 0;
        item.installs = 0;
        item.firstOpens = 0;
        item.reOpens = 0;
        item.signups = 0;
        item.stats = null;
        let url =
          "https://firebasedynamiclinks.googleapis.com/v1/https%3A%2F%2Ftunekey.app%2Flink%2F" +
          item.link.split("https://tunekey.app/link/")[1] +
          "/linkStats?durationDays=" +
          days;
        let signupDoc = await collections.signUpViaLinkCount.doc(item.id).get();
        item.signups = signupDoc.data().count;
        let stats = await self.getStats(url);
        // console.log("stats", stats);
        if (!self.$tools.isNull(stats)) {
          item.stats = stats;
          for (let j = 0; j < stats.length; j++) {
            if (stats[j].event === "CLICK") {
              item.clicks += parseInt(stats[j].count);
            } else if (stats[j].event === "REDIRECT") {
              item.redirects += parseInt(stats[j].count);
            } else if (stats[j].event === "APP_INSTALL") {
              item.installs += parseInt(stats[j].count);
            } else if (stats[j].event === "APP_FIRST_OPEN") {
              item.firstOpens += parseInt(stats[j].count);
            } else if (stats[j].event === "APP_RE_OPEN") {
              item.reOpens += parseInt(stats[j].count);
            }
          }
        }
        // console.log(i, item);
        self.data.push(item);
      }
      self.$emit("analytic");
    },
    getStats(url) {
      let self = this;
      return new Promise((resolve, reject) => {
        let func = self.$firebase
          .functions()
          .httpsCallable("getStatsFromDynamicLink");
        //https://tunekey.app/link/KvJh
        func({
          token: self.accessToken,
          url: url,
        })
          .then(async (result) => {
            resolve(JSON.parse(result.data.data.stats).linkEventStats);
          })
          .catch((err) => {
            console.log(err);
            resolve({});
          });
      });
    },
    async initStats(row) {
      let self = this;
      let link = row.link;
      self.stats = null;
      self.statsLink = link;
      let signupDoc = await collections.signUpViaLinkCount.doc(row.id).get();
      self.signups = signupDoc.data().count;
      let url =
        "https://firebasedynamiclinks.googleapis.com/v1/https%3A%2F%2Ftunekey.app%2Flink%2F" +
        link.split("https://tunekey.app/link/")[1] +
        "/linkStats?durationDays=365";
      self.stats = await self.getStats(url);
    },
    initLink(link) {
      let str1 = link.substring(0, 15),
        str2 = link.substring(link.length - 4, link.length);
      // return str1 + "..." + str2;
      return str2;
    },
    isExpired(row) {
      let timestamp =
        row.expireIn === "" || row.expireIn === 0
          ? new Date().getTime()
          : row.expireIn * 1000;
      if (timestamp < new Date().getTime()) {
        // 
        return true;
      } else {
        // 
        return false;
      }
    },
    saveName(row) {
      if (!this.$tools.isNull(row.name)) {
        collections.salesManParams
          .doc(row.id)
          .update({
            name: row.name,
          })
          .then(() => {
            console.log("name ");
          });
      } else {
        row.name = this.name;
      }
    },
    saveEmail(row) {
      if (!this.$tools.isNull(row.email)) {
        if (this.$tools.isEmail(row.email)) {
          collections.salesMan
            .doc(row.salesManId)
            .set({
              salesManId: row.salesManId,
              email: row.email,
            })
            .then(() => {
              console.log("email ");
            });
        } else {
          this.$notify({
            message: "Wrong email, please check it!",
            type: "error",
          });
          row.email = this.email;
        }
      } else {
        row.email = this.email;
      }
    },
    onStats(row) {
      alert("Stats");
    },
    resetExpiration(row) {
      let self = this;
      if (self.isExpired(row)) {
        self.onEnable(row);
      } else {
        self.onArchive(row);
      }
    },
    onArchive(row) {
      // alert("Archive");
      this.showArchive = true;
      this.row = row;
    },
    archiveCoupon() {
      let self = this;
      let expireIn = parseInt(
        (new Date().getTime() - 24 * 60 * 60 * 1000) / 1000
      );

      collections.salesManParams
        .doc(self.row.id)
        .update({
          expireIn: expireIn,
        })
        .then(() => {
          self.isButtonLoadingComplete = true;
          self.data.some((item) => {
            if (item.id === self.row.id) {
              item.expireIn = expireIn;
            }
          });
          self.row = null;
          self.showArchive = false;
        });

      self.isButtonLoadingComplete = false;
    },
    onEnable(row) {
      // alert("Enable");
      this.showEnable = true;
      this.expirationOpen = false;
      this.row = row;
    },
    enableCoupon() {
      let self = this;
      let expireIn =
        self.expiration === ""
          ? 0
          : parseInt(new Date(self.expiration).getTime() / 1000);

      collections.salesManParams
        .doc(self.row.id)
        .update({
          expireIn: expireIn,
        })
        .then(() => {
          self.isButtonLoadingComplete = true;
          self.expiration = "";
          self.data.some((item) => {
            if (item.id === self.row.id) {
              item.expireIn = expireIn;
            }
          });
          self.row = null;
          self.showEnable = false;
        });

      self.isButtonLoadingComplete = false;
    },
    onSendPDF(row) {
      htmlToPdf.downloadPDF(
        document.querySelector("#" + row.id),
        row.name + " - PDF"
      );
    },
    onCopySuccess() {
      this.$notify({
        message: "Copy Success!",
        type: "success",
      });
    },
    onCopyError() {
      this.$notify({
        message: "Copy Failed!",
        type: "error",
      });
    },
    initName(name) {
      if (this.$tools.isNull(name)) {
        return "no name";
      } else {
        return name;
      }
    },
    initEmail(data) {
      let self = this;
      let email = "no email";

      if (self.$tools.isNull(data)) {
        return "no email";
      } else {
        return email;
      }
    },
    initTime(time) {
      if (time == 0) {
        return "0 month";
      } else if (time == 1) {
        return "1 month";
      } else {
        return time + " months";
      }
    },
    initFreeMonths(time) {
      if (time == 0) {
        return "FREE DOWNLOAD";
      } else {
        return time + "-MONTH-FREE";
      }
    },
    initExpiration(expireIn) {
      if (this.$tools.isNull(expireIn) || expireIn === 0) {
        return "1/1/2099";
      } else {
        let timestamp = expireIn * 1000;
        return this.$tools.formatMMDDYY(timestamp);
      }
    },
    initCreated(time) {
      // console.log(time)
      let timestamp = time * 1000;
      return this.$tools.formatMMDDYY(timestamp);
    },
    isNull(obj) {
      return this.$tools.isNull(obj);
    },
    formatMMDDYY(str) {
      return this.$tools.formatMMDDYY(new Date(str).getTime());
    },
  },
};
</script>
